@import "https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap";
:root {
  --very-dark-gray: #2b2b2b;
  --dark-gray: #969696;
  --font-size-text-input: 18px;
  --font-weight-small: 400;
  --font-weight-medium: 500;
  --font-weight-large: 700;
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  height: auto;
  font-family: Rubik, sans-serif;
  overflow: hidden;
}

.container {
  min-height: 100%;
  height: auto;
  flex-direction: column;
  justify-content: stretch;
  display: flex;
  position: relative;
}

.backsplash {
  width: 100%;
  height: 300px;
  background-image: url("pattern-bg.93056d32.png");
  background-position: 56% 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.map {
  min-height: 524px;
  height: calc(100vh - 300px);
  z-index: 10;
  flex-grow: 1;
}

.main {
  min-height: 164px;
  height: 430px;
  width: 330px;
  text-align: center;
  z-index: 20;
  position: absolute;
  top: 28px;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  color: #fff;
  font-weight: var(--font-weight-medium);
  margin-bottom: 30px;
  font-size: 26px;
}

.form {
  width: 100%;
  max-width: 560px;
  height: 60px;
  border-radius: 12px;
  margin: 0 auto 24px;
  overflow: hidden;
}

.form__field {
  width: 100%;
  height: 100%;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  display: flex;
}

.form__field--input {
  height: 100%;
  font-size: var(--font-size-text-input);
  font-weight: var(--font-weight-small);
  color: var(--very-dark-gray);
  border: none;
  flex-grow: 1;
  padding-left: 26px;
  font-family: inherit;
}

.form__field--input:hover {
  cursor: pointer;
}

.form__field--input:focus {
  outline: none;
}

.form__field--input::placeholder {
  font-size: 14px;
  font-weight: var(--font-weight-small);
}

.form__field--button {
  width: 60px;
  height: 100%;
  color: #fff;
  background-color: #000;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form__field--button:hover {
  cursor: pointer;
  background-color: var(--very-dark-gray);
}

.fa-slid {
  color: #fff;
}

.data {
  height: auto;
  width: 100%;
  max-width: 1114px;
  background-color: #fff;
  border-radius: 12px;
  padding: 28px;
  overflow: hidden;
}

.data__field:not(:last-of-type) {
  margin-bottom: 22px;
}

.data__field--label {
  color: var(--dark-gray);
  text-transform: uppercase;
  font-size: 10px;
  font-weight: var(--font-weight-medium);
  letter-spacing: 1px;
  margin-bottom: 8px;
}

.data__field--value {
  font-size: 18px;
  font-weight: var(--font-weight-medium);
  color: var(--very-dark-gray);
}

@media (min-width: 640px) {
  .main {
    width: 480px;
  }
}

@media (min-width: 768px) {
  .main {
    width: 640px;
  }

  .data {
    height: 240px;
    flex-flow: wrap;
    justify-content: space-around;
    align-items: center;
    display: flex;
  }

  .data__field {
    width: 200px;
  }
}

@media (min-width: 992px) {
  .backsplash {
    height: 280px;
    background-position: center;
    background-size: cover;
  }

  .map {
    height: calc(100vh - 280px);
  }

  .main {
    width: 768px;
  }

  .form {
    margin-bottom: 48px;
  }

  .data {
    height: auto;
    text-align: left;
    align-items: flex-start;
  }

  .data__field {
    width: 150px;
    padding-top: 10px;
  }
}

